<template>
  <b-card
    fluid
    class="pb-2 px-2 pt-1"
  >
    <b-row class="border-bottom">
      <b-col
        cols="12"
      >
        <h3 class="text-primary">
          <b-badge variant="primary" /><feather-icon
            icon="LayersIcon"
            size="16"
          />  Actividades de Recreación y Deporte
        </h3>
      </b-col>

    </b-row>
    <br>
    <!-- User Interface controls -->

    <div class="m-2">

      <!-- Table Top -->
      <b-row>

        <!-- Per Page -->
        <b-col
          cols="12"
          md="6"
          class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
        >
          <label>Mostrar</label>
          <v-select
            v-model="perPage"
            :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
            :options="pageOptions"
            :clearable="false"
            class="per-page-selector d-inline-block mx-50"
          />
          <label>registros</label>
        </b-col>

        <!-- Search -->
        <b-col
          cols="12"
          md="6"
        >
          <div class="d-flex align-items-center justify-content-end">
            <b-form-input
              v-model="filter"
              size="md"
              type="search"
              class="d-inline-block mr-1"
              placeholder="Buscar..."
              debounce="500"
            />
            <b-button
              variant="outline-primary"
              :to="{ name: 'actividad-deportiva-nuevo', params: { id: 0 }}"
            >
              <span class="text-nowrap"><feather-icon icon="PlusCircleIcon" /> Nueva Actividad Deportiva</span>
            </b-button>
          </div>
        </b-col>
      </b-row>

    </div>

    <b-table
      id="my-table"
      ref="refTablaConvocatorias"
      class="position-relative"
      show-empty
      head-variant="light"
      hover
      small
      responsive
      :busy.sync="isBusy"
      :items="myProvider"
      :fields="fields"
      :current-page="currentPage"
      :empty-text="'No hay registros para mostrar'"
      :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
    >

      <template #table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle" />
          <strong>Cargando...</strong>
        </div>
      </template>

      <template #cell(acciones)="row">
        <b-dropdown
          variant="link"
          no-caret
          :right="$store.state.appConfig.isRTL"
        >
          <template #button-content>
            <feather-icon
              icon="MoreVerticalIcon"
              size="16"
              class="align-middle text-body"
            />
          </template>
          <b-dropdown-item
            :to="{ name: 'actividad-deportiva-nuevo', params: { id: row.item.id } }"
          >
            <feather-icon icon="EditIcon" />
            <span class="align-middle ml-50">Editar</span>
          </b-dropdown-item>
          <b-dropdown-item
            @click="eliminarActividad(row.item)"
          >
            <feather-icon icon="TrashIcon" />
            <span class="align-middle ml-50">Eliminar</span>
          </b-dropdown-item>
        </b-dropdown>
      </template>
      <template #cell(estado)="row">
        <b-badge
          pill
          :variant="`light-${resolveCitaStatusVariant(row.item.estado)}`"
        >
          {{ row.item.estado === 1 ? 'Activo' : 'Inactivo' }}
        </b-badge>
      </template>

    </b-table>
    <div class="mx-2 mb-2">
      <b-row>
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-start"
        >
          <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
        </b-col>
        <!-- Pagination -->
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-end"
        >
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            first-number
            last-number
            class="mb-0 mt-1 mt-sm-0"
            prev-class="prev-item"
            next-class="next-item"
          >
            <template #prev-text>
              <feather-icon
                icon="ChevronLeftIcon"
                size="18"
              />
            </template>
            <template #next-text>
              <feather-icon
                icon="ChevronRightIcon"
                size="18"
              />
            </template>
          </b-pagination>

        </b-col>
      </b-row>
    </div>

  </b-card>
</template>

<script>
import {
  BCard, BDropdown, BDropdownItem, BBadge, BButton, BSpinner, BFormInput, BRow, BCol, BPagination, BTable,
} from 'bootstrap-vue'
import store from '@/store'
import vSelect from 'vue-select'
import { onUnmounted } from '@vue/composition-api'
import { mapState } from 'vuex'
import actDeportivasStoreModule from './actDeportivasStoreModule'

export default {

  components: {
    vSelect,
    BButton,
    BDropdown,
    BDropdownItem,
    BSpinner,
    BFormInput,
    BCard,
    BRow,
    BCol,
    // BInputGroup,
    // BFormSelect,
    BPagination,
    BTable,
    // BInputGroupAppend,
    BBadge,
  },

  data() {
    return {
      isBusy: false,
      // items: [],
      totalRows: 0,
      from: 0,
      to: 0,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Ver todo' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
    }
  },
  computed: {
    ...mapState('actividades_depor', ['actividadesDeportivas', 'tabla']),
  },
  mounted() {

  },
  methods: {
    async myProvider(ctx) {
      const promise = store.dispatch('actividades_depor/fetchActividades', { queryParams: ctx })
      return promise.then(response => {
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data
        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0
        this.to = to || 0

        return items || []
      })
    },

    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },
    eliminarActividad(value) {
      this.$swal({
        title: `Esta seguro de eliminar la actividad "${value.nombre}"`,
        text: 'Esta acción es irreversible!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('actividades_depor/deleteActividad', value.id).then(() => {
            this.$swal({
              icon: 'success',
              title: 'Realizado!',
              text: 'Taller eliminado.',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
            store.dispatch('actividades_depor/fetchActividades', { id: this.id })
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },
  },
  setup() {
    const ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME = 'actividades_depor'

    // Register module
    if (!store.hasModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)) store.registerModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME, actDeportivasStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)) store.unregisterModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)
    })

    const fields = [

      { key: 'nombre', label: 'Actividad', sortable: true },
      { key: 'fecha', label: 'Fecha', sortable: true },
      { key: 'acciones', label: 'acciones' },
    ]
    return {
      fields,
    }
  },
}
</script>
<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
</style>
